import {
  Button,
  Card,
  CardContent,
  CardHeader,
  Container,
  Divider,
  Grid,
  Tab,
  Tabs
} from '@mui/material';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import useQueryJobByOwner from 'src/modules/jobs/hooks/useQueryJobByOwner';
import TablePost from 'src/pages/company-job-list/TablePost';
import TabsWrapper from 'src/components/TabWrapper';
import Pagination from 'src/components/Pagination';
import SuspenseLoader from 'src/components/SuspenseLoader';

export const tabs = [
  { label: 'Tt c', value: '' },
  { label: ' duyt', value: ' duyt' },
  { label: 'Ch duyt', value: 'Ch duyt' },
  { label: 'T chi', value: 'T chi' },
  { label: 'Ht hn', value: 'Ht hn' }
];

const JobList = () => {
  const pageSize = 9;
  const [currentTab, setCurrentTab] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [sortModel, setSortModel] = useState({ orderBy: '', sort: '' });
  const { jobs, totalResults, isLoading, totalPages } = useQueryJobByOwner({
    status: currentTab,
    page: currentPage,
    num: pageSize,
    orderBy: sortModel.orderBy,
    sort: sortModel.sort
  });

  const handleTabsChange = (e, value) => {
    setCurrentTab(value);
    setCurrentPage(1);
  };

  if (isLoading) return <SuspenseLoader />;
  return (
    <Container maxWidth="xl">
      <Grid
        container
        direction="row"
        justifyContent="center"
        alignItems="stretch"
        spacing={3}
        marginTop={0}
      >
        <Grid item xs={12}>
          <Card>
            <CardHeader
              title="Danh Sch Tin Tuyn Dng"
              action={
                <Link to={'/employer/recruitment/create'}>
                  <Button variant="contained" size="small" sx={{ my: 0 }}>
                    To tin tuyn dng
                  </Button>
                </Link>
              }
            />
            <Divider />
            <CardContent>
              <TabsWrapper
                onChange={handleTabsChange}
                value={currentTab}
                variant="scrollable"
                scrollButtons={false}
                sx={{
                  display: { md: 'inline-block' },
                  borderBottom: 1,
                  borderColor: 'divider'
                }}
              >
                {tabs.map((tab) => {
                  return (
                    <Tab key={tab.value} label={tab.label} value={tab.value} />
                  );
                })}
              </TabsWrapper>
              <TablePost
                data={jobs || []}
                pageSize={pageSize}
                setSortModel={setSortModel}
              />
              <Pagination
                currentPage={currentPage}
                totalPages={totalPages}
                handlePageChange={setCurrentPage}
              />
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Container>
  );
};

export default JobList;
